

.header {
  background-color: #515375;
  min-height: 2vh;
  padding: 2em;
  color: white;
}
.form-text{
  visibility: hidden;
}

.loader {
  margin: 0px auto;
  border: 5px solid #f3f3f3; /* Light grey */
  border-top: 5px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.btn {
  padding: 0.5rem;
  margin:  0.3rem;

  border:  0.1rem  solid #d2ded3;
  border-radius:  0.3rem;

  background-color: #37a940;
  color: aliceblue;

  font-size: 0.9rem;
  font-weight: bold;

}

.btn:active{
  background-color: #087a11;

}

.input{
  padding: 0.5rem;
  margin:  0.3rem;

  width: 100%;

 

  border:  0.1rem  solid #d2ded3;
  border-radius:  0.3rem;
}
label {
  display: block;
}


form{
  margin: 0rem auto;  
  width: 50%;
}
section{
  margin: 1rem auto;
  padding:  1rem;
  align-items: center;
  align-content: center;
  border:  0.1rem  solid #d2ded3;
  border-radius:  0.3rem;
  width: 50%;
  background-color: #d2ded3;
}
.container{
height: auto;
}
.Message{
  width: 100%;
  height: 130px;
  text-align: center;
  color: rgb(0, 120, 224);

  font-size: 1rem;
  font-weight: bold;
}
.footer {
  position: relative;
  bottom: 0;
  width: 100%;
  background-color: #515375;
  padding: 2rem 0rem;
  text-align: center;
  color: white;
}
